

  .subscription-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 80px); /* Adjust based on your header height */
    background-color: #f6f9fc;
  }
  
  .subscription-card {
    background: white;
    padding: 40px;
    border-radius: 4px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 400px;
  }
  
  h2 {
    text-align: center;
    color: #32325d;
    font-weight: 400;
    margin-bottom: 20px;
  }
  
  .price {
    text-align: center;
    font-size: 28px;
    color: #32325d;
    margin-bottom: 20px;
  }
  
  .price span {
    font-size: 16px;
    color: #8898aa;
  }
  
  .card-element-container {
    border: 1px solid #e6e6e6;
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 20px;
  }
  
  .subscribe-btn {
    background: #5469d4;
    color: white;
    border: none;
    padding: 12px 16px;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    display: block;
    width: 100%;
  }
  
  .subscribe-btn:hover {
    background: #4a5fc1;
  }
  
  .subscribe-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }